<template>
    <div>
        <a-modal
      title=""
      v-model="visible"
      @ok="handleOk"
    >
      <div>
        excel
        
      </div>
    </a-modal>
        <div class="title"></div>
        <ul class="flex">
            <li class="flex">
                ID
                <a-input placeholder="ID" style="width:150px ;margin-left:10px;"/>
            </li>
            <li class="flex">
                
                <a-input placeholder=""  style="width:150px;margin-left:10px;"/>
            </li >
            <li class="flex">
                
                <a-input placeholder=""  style="width:150px;margin-left:10px;"/>
            </li>
             <li class="flex">
                
                <a-input placeholder=""  style="width:150px;margin-left:10px;"/>
            </li>
            <a-button icon="search"  style="margin-right:10px; margin-left:20px;" type="primary"></a-button>
        </ul>  


        <div class="title" style="margin-top:20px;"></div>
            
        <div style="margin-bottom:20px">
            <a-button icon="search"  style="margin-right:10px;" @click="preview"></a-button>
            <a-button icon="edit"    style="margin-right:10px;"></a-button>
            <a-button icon="delete"  style="margin-right:10px;"></a-button>
            <a-button icon="laptop"  style="margin-right:10px;" @click="showModal"></a-button>
            <a-button icon="laptop"  style="margin-right:10px;" @click="preserve"></a-button>
        </div>
        <div >
            <a-table :rowSelection="rowSelection" :columns="columns" :dataSource="data" >
                <a slot="j_id" slot-scope="text" href="javascript:;">{{text}}</a>
            </a-table>
        </div>
        <a-modal 
         title=""
          width='1100px'
         :visible="vfd_show"
          keyboard:false
          footer:false
        >
             <vfd></vfd>
        </a-modal>
 
    </div>
</template>

<script>

    import vfd from 'vfd'

const columns = [
  {
    title: 'ID',
    dataIndex: 'j_id',
    scopedSlots: { customRender: 'j_id' },
  }, 
  {
    title: '',
    dataIndex: 'j_code',
  }, 
  {
    title: '',
    dataIndex: 'j_name',
  },
  {
    title: '',
    dataIndex: 's_num',
  },
  {
    title: '',
    dataIndex: 's_name',
  },
  {
    title: '',
    dataIndex: 'v_num',
  },
  {
    title: '',
    dataIndex: 'v_name',
  },
  {
    title: '',
    dataIndex: 'u_cate',
  }
];
const data = [{
  key: '1',
  j_id: 'EPP0001',
  j_code:'E150',
  j_name:'MAC',
  s_num:'60120000002',
  s_name:'',
  v_num:'01',
  v_name:'MAC',
  u_cate:'	 ',
  // j_cate:''

},{
  key: '2',
  j_id: 'EPP0002',
  j_code:'E112',
  j_name:'',
  s_num:'60120000002',
  s_name:'',
  v_num:'01',
  v_name:'MAC',
  u_cate:'	 ',
  // j_cate:''

}];
export default {
  data() {
    return {
      data,
      columns,
      visible: false,
      vfd_show:false
    }
  },
  components:{
        vfd
  },
 computed: {
    rowSelection() {
      const { selectedRowKeys } = this;
      return {
        onChange: (selectedRowKeys, selectedRows) => {
          console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
        },
        // getCheckboxProps: record => ({
        //   props: {
        //     disabled: record.name === 'Disabled User', // Column configuration not to be checked
        //     name: record.name,  
        //   }
        // }),
      }
    }
 },
  methods: {
    showModal(){
      this.visible = true
    },
    handleOk(e) {
      console.log(e);
      this.visible = false
    },
    preserve(){
      console.log('');
      this.$emit('createP','')
    },
    preview(){
      console.log('');
      // this.$emit('createV','
      this.vfd_show = true;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.title{
    font-size: 20px;
    font-weight: bold;
    margin-bottom:20px;
}
.flex{
    display: flex;
    display:-webkit-flex;
    align-items: center;
}
ul{
    list-style: none;
    padding:0;
}
li{
    margin-right: 10px; 
}
#components-layout-demo-top-side-2 .logo {
  width: 120px;
  height: 31px;
  background: rgba(255,255,255,.2);
  margin: 16px 28px 16px 0;
  float: left;
}
.table-operations {
  margin-bottom: 16px;
}

.table-operations > button {
  margin-right: 8px;
}
</style>



